import axios from '../../axios'
import {bus} from '../../main'

const state = {
  token: localStorage.getItem('hauthid') || null,
  channelID: "",
  userDetails: null,
}

const getters = {
  tokenExists(state) {
    return state.token !== null;
  },
  userDetails(state){
    return state.userDetails;
  },
  loggedIn(state){
    return state.userDetails !== null
  }
}

const actions = {
  login(context, credentials) {
    //console.log(context.rootState)
    return new Promise((resolve, reject) => {
      axios({
        url: "/user/login",
        method: "post",
        data: {email: credentials.email, password: credentials.password}
      })
      .then(response => {
        const token = response.data.token;
        context.commit('token', token);
        context.commit('userDetails', response.data.user)
        resolve(response)
      })
      .catch(error => {
        reject(error)
      })
    })
  },
  
  signup(context, account) {
    return new Promise((resolve, reject) => {
      axios({
        url: "/user/signup",
        method: "post",
        data: {email: account.email, username: account.username, password: account.password}
      })
      .then(response => {
        // Save token to local storage.
        const token = response.data.token;
        axios.defaults.headers.common['authorization'] = token;
        context.commit('token', token);
        context.commit('userDetails', response.data.user)
        resolve(response)
      }).catch(error => {
        reject(error)
      })
    })
  },

  getDetails(context) {
    return new Promise((resolve, reject) => {
      axios({
        url: "/user/details",
        method: "get"
      })
      .then (response => {
        resolve(response)
        context.commit('userDetails', response.data.user)
      })
      .catch(error => {
        if (error.response === undefined) {
          // retry if connection failed
          setTimeout(() => {
            context.dispatch('getDetails');
          }, 5000);
          return;
        }
        context.commit('logout');
        reject(error)
      })
    })
  },

  addFriend(context, data) {
    return new Promise((resolve, reject) => {
      axios({
        url: "/user/relationship",
        method: "post",
        data: {username: data.username, tag: data.tag}
      })
      .then (response => {

      })
      .catch(error => {
        
      })
    }) 
  }
}

const mutations = {
  token(state, token) {
    axios.defaults.headers.common['authorization'] = token;
    localStorage.setItem('hauthid', token);
    state.token = token
  },
  logout(state) {
    axios.defaults.headers.common['authorization'] = "";
    localStorage.removeItem('hauthid')
    state.userDetails = null,
    state.token = null;
  },
  userDetails(state, user) {
    state.userDetails = user;
  }
}

export default {
  namespace: true,
  state,
  getters,
  actions,
  mutations
}