<template>
  <div class="add-friend-panel">
    <div class="panel-title" @click="expanded = !expanded">{{expanded ? "Hide" : "Add friend"}}</div>
    <transition name="slide" appear>
      <div class="add-friend" v-if="expanded">
        <div class="title">Add friend</div>
        <div class="info">Type in your friends username and tag. eg: someone@jt4g</div>
        <form action="#" @submit.prevent="addFriend">
          <input type="text" placeholder="username@tag" v-model="input">
          <loadingButton :loading="currentMessage == 1" :message="buttonMessages[currentMessage]" />
        </form>
        <div class="message warning">
          Message here :D
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import loadingButton from './../../Button.vue'
export default {
  components: {
    loadingButton
  },
  data() {
    return {
      expanded: false,
      buttonMessages: [
        "Add Friend",
        "Adding..."
      ],
      currentMessage: 0,
      input: ""
    }
  },
  methods: {
    addFriend() {
      this.currentMessage = 1;
    }
  }
}
</script>


<style scoped>
.slide-enter-active, .slide-leave-active {
  transition: .3s;
}
.slide-enter, .slide-leave-to /* .fade-leave-active below version 2.1.8 */ {
  margin-bottom: -250px;
  opacity: 0;
}

.add-friend-panel{
  width: 100%;
  background: rgba(47, 47, 47, 0.295);
  display: flex;
  flex-direction: column;
}

.add-friend{
  background: rgba(41, 41, 41, 0.212);
  flex: 1;
  height: 250px;
  display: flex;
  flex-direction: column;
  color: white;
}
.title{
  margin: auto;
  margin-top: 10px;
  margin-bottom: 0px;
  font-size: 20px;
  color: white;
  user-select: none;
  cursor: default;
}
.info{
  text-align: center;
  color: rgb(182, 182, 182);
  font-size: 15px;
  user-select: none;
  cursor: default;
}
form {
  margin: auto;
  margin-top: 5px;
  margin-bottom: 0px;
}

.message{
  margin: auto;
  margin-top: 2px;
  font-size: 15px;
  color: green;
}
.warning {
  color: red;
}
.panel-title{
  background: rgba(47, 47, 47, 0.472);
  width: 100%;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 5px;
  color: white;
  cursor: pointer;
  user-select: none;
}

</style>
