<template>
  <div class="right-panel">
    <div class="heading">
      <div class="show-menu-button" cli>
        <i class="material-icons" @click="toggleLeftMenu">
        menu
        </i>
      </div>
      <div class="current-channel">No channel selected</div>
    </div>
    <div class="message-logs">
      <Message v-for="msg in messageLogs[channelID]" :key="msg.messageID" :message="msg.message" :status="msg.status" /> 
    </div>

    <div class="chat-input-area">
      <textarea class="chat-input" ref="input-box" placeholder="Message" @keydown="chatInput" v-model="message"></textarea>
      <button class="send-button" @click="sendMessage">Send</button>
    </div>
  </div>
</template>

<script>
import {bus} from '../../main'
import JQuery from 'jquery'
let $ = JQuery

import Message from '../../components/app/MessageTemplate.vue'
export default {
  components: {
    Message
  },
  data() {
    return {
      message: ""
    }
  },
  methods:{
    toggleLeftMenu(){
      bus.$emit('toggleLeftMenu')
    },
    sendMessage(){
      this.$refs["input-box"].focus()
      if(this.message.trim() == "")return;
      this.$store.commit('sendMessage', this.message);
      this.message = ""

      //Scroll to bottom
      $(".message-logs").stop(true).animate({
        scrollTop: $(".message-logs")[0].scrollHeight
      }, 300);
    },
    chatInput(event) {
      if (event.keyCode == 13) {
        event.preventDefault();
        this.sendMessage();
      }
    }
  },
  computed: {
    messageLogs() {
      return this.$store.state.messageLogs
    },
    channelID() {
      return this.$store.state.channelID
    }
  }
}
</script>



<style scoped>
.heading{
    border-bottom: solid 2px white;
    margin: 5px;
    height: 40px;
    padding-bottom: 2spx;
    display: flex;
    flex-shrink: 0;
}

.show-menu-button{
  display: inline-block;
  margin: auto;
  color: white;
  margin-left: 10px;
  margin-right: 5px;
  margin-top: 8px;
  user-select: none;
  display: none;
}

.heading .current-channel{
  color: white;
  font-size: 20px;
  margin: auto;
  margin-left: 5px;
  flex: 1;
}

.right-panel {
    height: 100%;
    background-color: rgba(0, 0, 0, 0.507);
    flex: 1;
    display: flex;
    flex-direction: column;
}
.message-logs{
    overflow: auto;
    flex: 1;
}

.chat-input-area{
    height: 60px;
    display: flex;
}
.chat-input{
    font-family: 'Roboto', sans-serif;
    background: rgba(0, 0, 0, 0.158);
    color: white;
    flex: 1;
    height: 20px;
    padding: 10px;
    margin: auto;
    margin-left: 20px;
    font-size: 15px;
    resize: none;
    border: none;
    outline: none;
    padding-left: 10px;
    transition: 0.3s;
}

.chat-input:hover{
    background: rgba(0, 0, 0, 0.288);
}

.chat-input:focus{
    background: rgba(0, 0, 0, 0.466);
}

.send-button{
    font-size: 20px;
    color:white;
    background: rgba(0, 0, 0, 0.274);
    border: none;
    outline: none;
    margin: auto;
    margin-left: 2px;
    margin-right: 20px;
    height: 40px;
    transition: 0.3s;
}
.send-button:hover{
        background: rgba(0, 0, 0, 0.514);
}

@media (max-width: 600px) {
  .show-menu-button{
    display: block;
  }
}
</style>

